:root {
  --theme-color: #006699;
  font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

.root {
  border: 0.75px solid lightgray;
  align-items: center;
  padding: 0 1.5em 1.5em;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 5em;
}

.root > h1 {
  color: #006699;
  color: var(--theme-color);
  font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

.widget-container {
  color: #006699;
  color: var(--theme-color);
  font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  margin: 0 auto;
  padding: 0;
  width: 600px;
  line-height: 1em;
}

.widget-container > form {
  display: inline-block;
  text-align: center;
  background-color: #006699;
  background-color: var(--theme-color);
  color: white;
  margin: 0;
  padding: 10px 15px;
  width: 600px;
  border-radius: 4px;
}

.widget-container > form > h3 {
