}

.widget-container > form > input {
  display: inline-block;
  vertical-align: top;
  position: relative;
  width: 33%;
  height: 36px;
  padding-left: 10px;
  font-size: 16px;
  border: none;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  font-family: Arial, sans-serif;
  color: black;
}

.widget-container form input::-webkit-input-placeholder {
